import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import store from './store';
import axios from 'axios'

Vue.use(Router)

const router =  new Router({
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      meta: {
        title: '首页',
        isAuth: true,
      }
    },
    {
      path: '/grouplists',
      name: 'grouplists',
      component: () => import('./views/Group/Lists.vue'),
      meta: {
        title: '预算项目',
        isAuth: true,
      }
    },
    {
      path: '/groupfees',
      name: 'groupfees',
      component: () => import('./views/Group/Fees.vue'),
      meta: {
        title: '预算项目',
        isAuth: true,
      }
    },
    {
      path: '/incomings',
      name: 'incomings',
      component: () => import('./views/Group/Incomings.vue'),
      meta: {
        title: '收入目标',
        isAuth: true,
      }
    },
    {
      path: '/typelists',
      name: 'typelists',
      component: () => import('./views/Group/Types.vue'),
      meta: {
        title: '类型维护',
        isAuth: true,
      }
    },
    {
      path: '/exchange',
      name: 'exchange',
      component: () => import('./views/Group/Exchange.vue'),
      meta: {
        title: '预算调整',
        isAuth: true,
      }
    },
    {
      path: '/staffprocess',
      name: 'staffprocess',
      component: () => import('./views/Process/Staff.vue'),
      meta: {
        title: '员工出差流程',
        isAuth: true,
      }
    }
  ]
})

router.beforeEach(async (to, from, next) => {
  let expire = Number(localStorage.getItem('expire')) ||0;
  
  if(to.meta.title) {
    document.title = to.meta.title;
  }

  if (to.matched.some(record => record.meta.isAuth)) {
    let token = store.state.token || localStorage.getItem('token');
    if(!token || expire < Date.now()) {
      if(to.query.userId) {
        let res = await axios.get(`/ec/api/auth/login?userId=${to.query.userId}`)
        let resData = res.data;
        if (resData.errcode === 0) {
          let user = resData.data.user;
          let token = resData.data.token;
  
          localStorage.setItem('user', JSON.stringify(user));
          localStorage.setItem('token', token);
          localStorage.setItem('expire', Date.now() + 24 * 60 * 60 * 1000)

          store.commit('setUser', user);
          window.location.reload()
        }
      }
    }
  }
  next()
})

export default router;
